@import "normalizes.scss";
@import "resets.scss";

* {
    box-sizing: border-box; }

@font-face {
    font-family: 'MontserratT';
    src: url("../fonts/Montserrat-Regular.ttf");
    font-weight: regular;
    font-style: normal; }

@font-face {
    font-family: 'MontserratT';
    src: url("../fonts/Montserrat-SemiBold.ttf");
    font-weight: bold;
    font-style: normal; }

html {
    font-family: 'MontserratT';
    font-weight: regular; }

$color-blue: #5290EA;
$color-orange: #F28B64;
$color-gradient: linear-gradient(143.92deg, rgba(237, 123, 80, 0.5) 14.85%, rgba(72, 138, 234, 0.5) 53.25%, rgba(44, 44, 44, 0.5) 85.74%);


.container {
    position: relative;
    max-width: 1140px;
    margin: 0 auto; }

body {
    min-height: 100%; }
.main-section {
    position: relative;
    min-height: 800px;
    background: #F0F0F0;
    padding-top: 16px;
    z-index: 1;
    overflow: hidden;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 69px;
        align-items: center; }
    &__logo {
        width: 134px; }
    &__content-container {
        position: relative;
        display: flex;
        -ms-flex-direction: row; }
    &__first-elipse {
        position: absolute;
        top: 129px;
        right: 33px; }
    &__second-elipse {
        position: absolute;
        right: -120px;
        top: 650px; } }


.nav-menu {
    &__list {
        display: flex;
        flex-direction: row;
        font-family: bold; }
    &__list-item {
        margin-right: 64px;
        font-size: 18px;
        &:last-child {
            margin-right: 0px; }
        a {
            text-decoration: none;
            color: black; } } }


.left-wrapper {
    position: relative;
    &__img {
        position: relative;
        z-index: 5; }
    &__rectangle-blue {
        position: absolute;
        top: 150px;
        left: -50px;
        width: 565.95px;
        height: 220px;
        background-color: $color-blue;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0,0.25);
        transform: rotate(-64deg);
        z-index: 1; }
    &__rectangle-orange {
        position: absolute;
        top: 160px;
        left: -30px;
        width: 618.43px;
        height: 220px;
        background-color: $color-orange;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0,0.25);
        transform: rotate(-50.81deg);
        z-index: 3; }
    &__rectangle-grey {
        position: absolute;
        top: 190px;
        left: 10px;
        width: 608.97px;
        height: 220px;
        background-color: #e2e2e2;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0,0.25);
        transform: rotate(-36.15deg);
        z-index: 1; } }


.right-wrapper {
    position: absolute;
    width: 372px;
    top: 150px;
    right: 76px;
    &__icons-box {
        margin-bottom: 1px;
        padding-left: 10px; }
    &__title {
        margin-bottom: 5px;
        font-family: bold; }
    &__text {
        font-size: 16px;
        margin-bottom: 20px; } }

.icons {
    display: flex;
    flex-direction: row;
    &__items {
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid $color-blue;
        border-radius: 100px;
        width: 40px;
        height: 40px;
        margin-right: 17px;

        &:last-child {
            margin-right: 0px; }
        i {
            font-size: 21px; } } }

.title {
    font-size: 72px;
    position: relative;
    &__line {
        width: 65px;
        position: absolute;
        bottom: 0px;
        border: 1px solid black;
        margin: 0; } }

.buttons {
    display: inline-flex;
    &__main-btn {
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background-color: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0,0.25);
        border-radius: 100px;
        border: none;
        font-size: 18px;
        color: white;
        font-family: bold; }
    &__icon-btn {
        display: inline-flex;
        align-items: center;
        img {
            margin-right: 12px; } }
    &__text {
       font-size: 16px; } }

.elipse {
    width: 194px;
    height: 194px;
    background: $color-gradient;
    border-radius: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: -1;
    &_dig {
        width: 282px;
        height: 282px; }
    &__subtract {
        width: 129px;
        height: 129px;
        background: #F0F0F0;
        border-radius: 150px;
        &_big {
            width: 188px;
            height: 188px; } } }

.second-section {
    min-height: 700px;
    overflow: hidden;
    &__container {
        position: relative; }
    &__cards-box {
        display: inline-flex; }
    &__title {
        margin-bottom: 25px;
        padding-top: 75px;
        font-family: bold; }
    &__text {
        font-size: 32px;
        text-align: center;
        transform: rotate(90deg);
        position: absolute;
        top: 130px;
        right: 60px; }
    &__icons-item {
        margin-right: 235px;
        &:last-child {
            margin-right: 0px; } }
    &__first-elipse {
        position: absolute;
        top: 90px;
        right: -85px;
        -webkit-filter: blur(4px)  /* Размытие */; }
    &__second-elipse {
        position: absolute;
        top: 220px;
        right: 1040px;
        -webkit-filter: blur(4px); } }

.cards-item {
    margin-right: 33px;
    position: relative;
    width: 360px;
    height: 315px;
    background-color: #E7E7E7;
    box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    align-items: flex-end;
    margin-bottom: 29px;
    &:last-child {
        margin-right: 0px; }
    &__img {
        display: flex;
        flex-direction: center; } }

.cards-item_orange {
    background-color: $color-orange; }

.cards-item_blue {
    background-color: $color-blue; }

.icons-item {
    display: inline-flex;
    align-items: center;
    &__img {
        width: 80px;
        margin-right: 16px; }
    &__text {
        font-size: 16px;
        span {
            font-size: 36px;
            font-family: bold; } } }

.third-section {
    min-height: 800px;
    background-color: #F2F2F2;
    &__container {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-top: 70px; } }
.left-wpapper-content {
    &__title {
        padding-left: 220px;
        padding-top: 29px;
        font-family: bold; }
    &__first-elipse {
        width: 112px;
        height: 112px;
        position: absolute;
        top: 420px;
        left: 470px;
        z-index: 1;
        -webkit-filter: blur(2px); }
    &__second-elipse {
        width: 158px;
        height: 158px;
        position: absolute;
        top: 255px;
        left: 125px;
        z-index: 1; }
    &__third-elipse {
        width: 125px;
        height: 125px;
        position: absolute;
        top: 200px;
        left: 210px;
        z-index: 0;
        .elipse__subtract {
            width: 62px;
            height: 62px; } }
    &__fird-elipse {
        width: 62px;
        height: 62px;
        position: absolute;
        z-index: 1;
        top: 530px;
        left: 590px;
        -webkit-filter: blur(4px); } }

.right-wrapper-content {
    max-width: 479px;
    &__title {
        margin-bottom: 20px;
        font-family: bold; }
    &__text {
        margin-bottom: 25px;
        font-size: 16px; } }
.title__line_bot {
    bottom: 80px;
    width: 35px; }

.title__line_dot {
    width: 45px; }

.footer {
    &__container {
        position: relative;
        min-height: 390px;
        display: flex;
        align-items: center;
        justify-content: flex-end; }
    &__img {
        position: absolute;
        left: -70px;
        top: -70px; }
    &__title {
        margin-bottom: 20px;
        font-family: bold; } }

